import { Typography } from '@material-tailwind/react'
import { Button } from "@material-tailwind/react";
import React, { Fragment } from 'react'

import userImage from "../../assests/profile-image.jpg"

const Showcase = () => {

    return (
        <Fragment>
            <section className='container mx-auto max-w-screen-xl py-2 px-4 lg:px-8 lg:py-4 center flex items-center justify-center body-light-color'>
                <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 container mx-auto flex items-center justify-end sm:py:5 lg:py-5">

                    <div className='center py-5 animate__animated animate__zoomIn'>
                        <img src={userImage} alt='Samuel Izuagbe Enakhe' className="lg:h-96 lg:w-96 h-80 w-80 rounded-full object-cover object-center" />
                    </div>

                    <div class="col-span-1 center sm:end animate__animated animate__fadeInRight">
                        <div className="">
                            <Typography variant="h4" className='font-family text-white'>Hi,</Typography>
                            <Typography variant="h1" className='font-family py-4 text-white'>
                                I’m Enakhe <br /> a <span className='text-color'>Software Developer</span>
                            </Typography>
                            <Button className="flex items-center primary-color gap-3 font-family">
                                <svg
                                    xmlns="http://www.w3.org/2000/svg"
                                    fill="none"
                                    viewBox="0 0 24 24"
                                    strokeWidth={2}
                                    stroke="currentColor"
                                    className="h-5 w-5"
                                >
                                    <path
                                        strokeLinecap="round"
                                        strokeLinejoin="round"
                                        d="M12 16.5V9.75m0 0l3 3m-3-3l-3 3M6.75 19.5a4.5 4.5 0 01-1.41-8.775 5.25 5.25 0 0110.233-2.33 3 3 0 013.758 3.848A3.752 3.752 0 0118 19.5H6.75z"
                                    />
                                </svg>
                                Get Resume
                            </Button>
                        </div>
                    </div>
                </div>

            </section>
        </Fragment>

    )
}

export default Showcase