import {
    List,
    ListItem,
    ListItemSuffix,
    Chip,
    Card,
} from "@material-tailwind/react";

import {
    CheckIcon
} from '@heroicons/react/24/solid';

const Icon = () => {
    return (
        <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none" viewBox="0 0 24 24"
            strokeWidth={2}
            stroke="currentColor"
            class="w-5 h-5 text-green">
            <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="m4.5 12.75 6 6 9-13.5" />
        </svg>
    );
}

const ListWithBadge = ({ title }) => {
    return (
        <Card className="w-full my-3">
            <List>
                <ListItem>
                    {title}
                    <ListItemSuffix>
                        <Chip
                            icon={<Icon />}
                            variant="ghost"
                            size="sm"
                            value=""
                            className="bg-transparent"
                        />
                    </ListItemSuffix>
                </ListItem>
            </List>
        </Card>
    )
}

export default ListWithBadge