import { Typography } from '@material-tailwind/react'
import { Fragment } from 'react'
import React from 'react'
import { BookingCard } from '../Utils/Card'
import { services } from '../../app/objects'
import ScrollAnimation from 'react-animate-on-scroll'



const Services = () => {
	return (
		<Fragment>
			<div className='body-dark-color'>
				<div className='container mx-auto max-w-screen-xl my-10 py-2 px-4 lg:px-8 lg:py-4'>
					<Typography variant="h5" className='font-family text-white py-3'>
						<ScrollAnimation animateIn='fadeInLeft' duration={1} animateOnce={true}>
							<span className='mr-3'>||</span>My Services
						</ScrollAnimation>
					</Typography>

					<ScrollAnimation animateIn='fadeInLeft' duration={1} animateOnce={true}>
						<p className="py-3 lg:text-4xl text-2xl font-family text-white">
							Service Provide For My Clients
						</p>
					</ScrollAnimation>

					<div class="grid grid-cols-1 sm:grid-cols-3 gap-4 container mx-auto flex items-center justify-end sm:py:5 lg:py-5">
						{
							services.map((ser, index) => (
								<ScrollAnimation animateIn='fadeInUp' duration={2} animateOnce={true}>
									<BookingCard title={ser.title} image={ser.image} desc={ser.desc} stack={ser.stacks} key={index} />
								</ScrollAnimation>
							))
						}
					</div>
				</div>
			</div>
		</Fragment >
	)
}

export default Services