import {
    Card,
    CardBody,
    CardFooter,
    Typography,
    Button,
    Avatar,
} from "@material-tailwind/react";

import image from '../../assests/user2.png'
import ProgressLabelOutside from "./ProgressLabelOutside";
import { grades } from "../../app/objects";

const CardWithLink = () => {
    return (
        <Card className="mt-6 mb-6 w-full body-dark-color">
            <CardBody>
                <Avatar src={image} alt="avatar" className="border border-[#d26a47] shadow-sm shadow-[#d26a47] ring-4 ring-[#d26a47] mb-3" size="xxl" />
                <Typography variant="h5" color="white" className="mb-2 font-family">
                    Samuel Izuagbe Enakhe
                </Typography>
                <Typography className="font-family text-white lg:text-2xl text-lg">
                    A thrilled young software engineer based in Lagos, Nigeria. Who love building programs, clean codes, and reading books...
                </Typography>
            </CardBody>
            <CardFooter className="pt-0">
                <Button className="flex items-center primary-color gap-3 font-family">
                    <svg
                        xmlns="http://www.w3.org/2000/svg"
                        fill="none"
                        viewBox="0 0 24 24"
                        strokeWidth={2}
                        stroke="currentColor"
                        className="h-5 w-5"
                    >
                        <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            d="M12 16.5V9.75m0 0l3 3m-3-3l-3 3M6.75 19.5a4.5 4.5 0 01-1.41-8.775 5.25 5.25 0 0110.233-2.33 3 3 0 013.758 3.848A3.752 3.752 0 0118 19.5H6.75z"
                        />
                    </svg>
                    Get Resume
                </Button>
            </CardFooter>
            <hr className="my-3 border-gray-200 sm:mx-auto lg:my-8 mx-3" />
            <div className="m-3">
                <div class="grid grid-cols-1 sm:grid-cols-3 gap-4 container mx-auto flex items-center justify-end sm:py:5 lg:py-5">
                    {
                        grades.map((grd, index) => (
                            <ProgressLabelOutside title={grd.name} score={grd.grade} color={grd.color} key={index} />
                        ))
                    }
                </div>

            </div>
        </Card>
    )
}

export default CardWithLink