import {
    Card,
    CardHeader,
    CardBody,
    CardFooter,
    Typography,
    Button,
    Tooltip,
} from "@material-tailwind/react";

export function BookingCard({ image, title, desc, stack }) {
    return (
        <Card className="w-full max-w-[26rem] shadow-lg my-6 body-light-color">
            <CardHeader floated={false} color="white">
                <img
                    src={image}
                    className="w-96 h-48"
                    alt={title} />
                <div className="to-bg-black-10 absolute inset-0 h-full w-full bg-gradient-to-tr from-transparent via-transparent to-black/60 " />
            </CardHeader>
            <CardBody>
                <div className="mb-3 flex items-center justify-between">
                    <Typography variant="h5" color="white" className="font-medium font-family">
                        {title}
                    </Typography>
                    <Typography
                        color="white"
                        className="flex items-center gap-1.5 font-normal font-family">
                        <svg
                            xmlns="http://www.w3.org/2000/svg"
                            viewBox="0 0 24 24"
                            fill="currentColor"
                            className="-mt-0.5 h-5 w-5 text-yellow-700"
                        >
                            <path
                                fillRule="evenodd"
                                d="M10.788 3.21c.448-1.077 1.976-1.077 2.424 0l2.082 5.007 5.404.433c1.164.093 1.636 1.545.749 2.305l-4.117 3.527 1.257 5.273c.271 1.136-.964 2.033-1.96 1.425L12 18.354 7.373 21.18c-.996.608-2.231-.29-1.96-1.425l1.257-5.273-4.117-3.527c-.887-.76-.415-2.212.749-2.305l5.404-.433 2.082-5.006z"
                                clipRule="evenodd"
                            />
                        </svg>
                        5.0
                    </Typography>
                </div>
                <Typography color="white" className="font-family">
                    {desc.split(" ").slice(0, 15).join(" ").concat("...")}
                </Typography>
                <center>
                    <div className="group mt-8 inline-flex flex-wrap items-center gap-3">
                        {
                            stack.map((stc, index) => (
                                <Tooltip content={stc.name} key={index}>
                                    <span className="cursor-pointer rounded-full border body-dark-color/5 bg-gray-900/5 p-3 text-white transition-colors hover:border-gray-900/10 hover:bg-gray-900/10 hover:!opacity-100 group-hover:opacity-70">
                                        <img
                                            src={stc.image}
                                            className="w-4 object-contain"
                                            alt={stc.nme} />
                                    </span>
                                </Tooltip>
                            ))
                        }
                    </div>
                </center>
                
            </CardBody>
            <CardFooter className="pt-3">
                <Button size="lg" fullWidth={true} className="primary-color font-family">
                    Hire
                </Button>
            </CardFooter>
        </Card>
    );
}