import React from 'react'
import { Progress, Typography } from "@material-tailwind/react";

const ProgressLabelOutside = ({ title, score, color }) => {
    return (
        <div className="w-full font-family my-3">
            <div className="mb-2 flex items-center justify-between gap-4">
                <Typography color="white" variant="h6" className='font-family'>
                    {title}
                </Typography>
                <Typography color="white" variant="h6" className='font-family'>
                    {score}%
                </Typography>
            </div>
            <Progress value={score} color={color} />
        </div>
    )
}

export default ProgressLabelOutside