import { Typography } from '@material-tailwind/react'
import { Fragment } from 'react'
import React from 'react'
import CardWithLink from '../Utils/CardWithLink'
import ScrollAnimation from 'react-animate-on-scroll';


const About = () => {
    return (
        <Fragment>
            <div className='bg-white'>
                <div className='container mx-auto max-w-screen-xl my-10 py-2 px-4 lg:px-8 lg:py-4'>

                    <Typography variant="h5" className='font-family text-gray-900 py-3'>
                        <ScrollAnimation animateIn='fadeInLeft' duration={1} animateOnce={true}>
                            <span className='mr-3'>||</span>About
                        </ScrollAnimation>
                    </Typography>

                    <ScrollAnimation animateIn='fadeInLeft' duration={1} animateOnce={true}>
                        <p className="py-3 lg:text-4xl text-2xl font-family text-gray-800">
                            About Myself
                        </p>
                    </ScrollAnimation>

                    <center>
                        <ScrollAnimation animateIn='fadeInLeft' duration={2} animateOnce={true}>
                            <CardWithLink />
                        </ScrollAnimation>
                    </center>
                </div>
            </div>
        </Fragment>
    )
}

export default About