import React from 'react'
import {
    Accordion,
    AccordionHeader,
    AccordionBody,
} from "@material-tailwind/react";
import ListWithBadge from './ListWithBadge';
import ScrollAnimation from 'react-animate-on-scroll';

const Icon = ({ id, open, text }) => {
    return (
        <>
            <div className="grid grid-cols-2 gap-4 items-center justify-stretch">
                <span className='text-xs lg:text-lg'>{text}</span>
                <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    viewBox="0 0 24 24"
                    strokeWidth={2}
                    stroke="currentColor"
                    className={`${id === open ? "rotate-180" : ""} float-end w-5 transition-transform`}>
                    <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        d="M13.5 6H5.25A2.25 2.25 0 0 0 3 8.25v10.5A2.25 2.25 0 0 0 5.25 21h10.5A2.25 2.25 0 0 0 18 18.75V10.5m-10.5 6L21 3m0 0h-5.25M21 3v5.25" />
                </svg>
            </div>
        </>
    );
}

const CUSTOM_ANIMATION = {
    mount: { scale: 1 },
    unmount: { scale: 0.9 },
};

const AccordionAllOpen = ({ title, desc, open, company, period }) => {
    const [openAcc, setOpenAcc] = React.useState(open);
    const handleOpenAcc = () => setOpenAcc((cur) => !cur);

    return (
        <Accordion open={openAcc} icon={<Icon id={1} open={open} text={company} />} className='text-white' color='white' animate={CUSTOM_ANIMATION}>
            <AccordionHeader onClick={handleOpenAcc} className='font-family justify-between lg:text-2xl text-sm text-white hover:text-white'>
                <span>{title}</span>
            </AccordionHeader>
            <AccordionBody className="font-family text-white">
                <span className='text-xs lg:text-lg'>{period}</span>
                {
                    desc.map((des, index) => (
                        <>
                            <ScrollAnimation animateIn='fadeInUp' duration={2} animateOnce={true}>
                                <ListWithBadge title={des.point} key={index} />
                            </ScrollAnimation>
                        </>
                    ))
                }
            </AccordionBody>
        </Accordion>
    )
}

export default AccordionAllOpen